@media screen and (min-width:991px) {
    .col-bottom-row-links:first-child {
        /* transform: translate(68%, -23%); */
        left: 180px;
        top: -135px;
    }

    .col-bottom-row-links:nth-child(2) {
        /* transform: translate(35%, -23%); */
        left: 72px;
        top: -135px;
    }

    .col-bottom-row-links:nth-child(3) {
        /* transform: translate(0%, -23%); */
        margin-top: -130px;
        z-index: 1;
    }
}

@media screen and (max-width:991px) {
    .col-bottom-row-links:first-child {
        /* transform: translate(0px,-40px); */
        -ms-flex: 0 0 80%;
        flex: 0 0 80%;
        max-width: 80%;
        left: 0px;
        margin-top: -60px;
    }

    .col-bottom-row-links:nth-child(2) {
        /* transform: translate(-60px, -40px); */
        margin-top: -300px;
        left: 400px;
        clip-path: polygon(55% 100%, 0 0, 100% 0);
        -webkit-clip-path: polygon(55% 100%, 0 0, 100% 0);
        -ms-clip-path: polygon(55% 100%, 0 0, 100% 0);
        -moz-clip-path: polygon(55% 100%, 0 0, 100% 0);
        -o-clip-path: polygon(55% 100%, 0 0, 100% 0);
    }

    .col-bottom-row-links:nth-child(3) {
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        max-width: 100%;
    }

    .col-bottom-row-links:nth-child(2) .heading {
        width: 150%;
        transform: translate(-17%, 0);
    }
	.col-bottom-row-links:nth-child(3) .heading {
    	margin-top: 50px;
	}
}

@media screen and (max-width:767px) {
    .col-bottom-row-links:first-child {
        /* transform: translate(0px,10px); */
        margin-top: 20px;
        -ms-flex: 0 0 70%;
        flex: 0 0 70%;
        max-width: 70%;
    }

    .col-bottom-row-links:nth-child(2) {
        /* transform: translate(0px, 10px); */
        left: 150px;
        margin-top: -300px;
        clip-path: polygon(25% 100%, 0 0, 100% 0);
    }

    .col-bottom-row-links:nth-child(2) .heading {
        transform: translate(-110px, 0);
        width: 120%;
    }

    .col-bottom-row-links:first-child .heading {
        transform: translate(5px, 70px);
    }
}
/* Editor styles for Applications Block */

.wp-block-acf-applications {
	margin: 1em 0;
}

.wp-block-acf-applications section {
	background: #fafafa;
	border: 1px solid #e0e0e0;
	border-radius: 4px;
}

.wp-block-acf-applications h2 {
	color: #3582c4;
	margin-top: 0;
}

.wp-block-acf-applications h3 {
	color: #555;
	font-size: 0.9rem;
	margin-top: 1.5em;
	margin-bottom: 0.5em;
}

.wp-block-acf-applications ul {
	margin: 0.5em 0;
	padding-left: 1.5em;
}

.wp-block-acf-applications li {
	margin: 0.25em 0;
	font-size: 0.9rem;
}

.wp-block-acf-applications p {
	margin: 0.5em 0;
	font-size: 0.9rem;
	color: #666;
}